















































































































































































import { mapFields } from 'vuex-map-fields'
import axios from 'axios'
import backend from '../utils/backend'
import Vue from 'vue'

import { store } from '../store'
import EventBus from '../events'

axios.defaults.xsrfCookieName = 'csrftoken'
axios.defaults.xsrfHeaderName = 'X-CSRFTOKEN'

export default Vue.extend({
  store,
  data() {
    return {
      formData: {
        first_name: '',
        last_name: '',
        email: '',
        email_confirm: '',
        control: '',
        profile_type: '',
      },
      postResult: [],
      errors: [],
      hasErrors: false,
      searchResult: {},
      foundUser: false,
      stepShown: 1,
      expectedEndingsArray: [],
    }
  },
  computed: {
    ...mapFields([
      'editingControl',
      'editingProfileType',
      'config.expected_inspector_email_endings',
      'config.site_url',
    ]),
    emailBody: function() {
      if (this.stepShown !== 3) {
        return ''
      }

      const newline = '%0d%0a'
      const body = 'Bonjour ' + this.postResult.first_name + ' ' + this.postResult.last_name + ',' +
        newline + newline + 'Je viens de vous ajouter au contrôle "' +
        this.editingControl.title +
        '" pour l\'organisme "' +
        this.editingControl.depositing_organization +
        '", en tant que membre de ' +
        (this.editingProfileType === 'inspector'
          ? 'l\'équipe de contrôle.'
          : 'l\'organisme contrôlé.') +
        newline + newline +
        'Pour vous connecter, rendez-vous sur le site d\'e.contrôle :' +
        newline + newline +
        this.site_url +
        newline + newline +
        'Cordialement,'

      return body
    },
  },
  methods: {
    cancel() {
      this.resetFormData()
      $('#addUserModal').modal('hide')
    },
    resetFormData() {
      this.formData = {
        first_name: '',
        last_name: '',
        email: '',
        email_confirm: '',
        control: '',
        profile_type: '',
      }
      this.stepShown = 1
      this.foundUser = false
      this.hasErrors = false
      this.errors = []
    },
    back() {
      switch (this.stepShown) {
        case 1.5:
          this.stepShown = 1
          break
        case 2:
          this.stepShown = 1
          break
        case 3:
          this.stepShown = 2
          break
        default:
          break
      }
    },
    validateEmail() {
      const expectedEndingsArray = this.expected_inspector_email_endings.split(',')
      const isInspectorEmail = email => {
        // At least one ending should match.
        return expectedEndingsArray.some(ending => {
          return email.endsWith(ending)
        })
      }

      if (this.editingProfileType === 'inspector' && !isInspectorEmail(this.formData.email)) {
        this.expectedEndingsArray = expectedEndingsArray
        this.stepShown = 1.5
      } else {
        if (this.formData.email !== this.formData.email_confirm) {
          this.hasErrors = true
          this.errors.email = ['Les deux champs e-mail doivent correspondre.']
        } else {
          this.hasErrors = false
          this.findUser()
        }
      }
    },
    addUser() {
      this.formData.control = this.editingControl.id
      this.formData.profile_type = this.editingProfileType
      this.formData.email = this.formData.email.toLowerCase()
      axios.post(backend.user(), this.formData)
        .then(response => {
          this.postResult = response.data
          EventBus.$emit('users-changed', this.postResult)
          this.stepShown = 3
        })
        .catch((error) => {
          this.hasErrors = true
          this.errors = error.response.data
        })
    },
    findUser() {
      this.formData.email = this.formData.email.toLowerCase()
      axios.get(backend.user(), {
        params: {
          search: this.formData.email,
        },
      })
        .then(response => {
          this.searchResult = response.data
          if (response.data.length) {
            this.foundUser = true
            Object.assign(this.formData, response.data[0])
          }
          this.stepShown = 2
        })
    },
  },
})
