




































import { mapFields } from 'vuex-map-fields'
import Vue from 'vue'
import Vuex from 'vuex'

import { store } from '../store'

Vue.use(Vuex)

export default Vue.extend({
  store,
  props: {
    users: Array,
    control: Object,
  },
  computed: {
    ...mapFields([
      'editingUser',
      'editingControl',
      'sessionUser',
    ]),
  },
  methods: {
    updateEditingState(user) {
      this.editingControl = this.control
      this.editingUser = {}
      Object.assign(this.editingUser, user)
    },
  },
})
